import React, { useState } from "react";
import './app.scss'
import { Header } from '../Header/Header';
import { SectionPics } from '../SectionPics/SectionPics';
import { SectionForm } from '../SectionForm/SectionForm';
import { VideoSection } from '../VideoSection/VideoSection';
import { Footer } from '../Footer/Footer';
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";
import { useCallback } from "react";
import { SectionMotivation } from "../SectionMotivation/SectionMotivation";
import { options } from "../../utils/utils.js"
import { useRef} from 'react'
import { Route, Routes, useNavigate } from "react-router-dom";
import { Admin } from "../Admin/Admin.jsx";

const App = () => {

  const scrollRef = useRef(null)
  const scrollRef2 = useRef(null)  
  const [glow, setGlow] = useState(false)
  const [users, setUsers] = useState([])
  const [showModal, setShowModal] = useState(false);

  const particlesInit = useCallback(async (engine) => {
    await loadFull(engine);
  }, []);

  const navigate = useNavigate()

  
  return (
    <>
    
  
    <Header setGlow={setGlow} scrollRef={scrollRef}/>
    <main className='content'>

    <Routes>
    <Route element={<Admin showModal={showModal} setShowModal={setShowModal} users={users} setUsers={setUsers}/>} path="/admin"></Route>

    <Route element={
      <div>
          <VideoSection scrollRef2={scrollRef2}/>
          <SectionMotivation ref={scrollRef2}/>
          <SectionPics/>
          <SectionForm users={users} setUsers={setUsers} glow={glow} setGlow={setGlow} ref={scrollRef}/>
      </div>
          } path="/">
      </Route>
      <Route
        path="*"
        element={
          <div  className="container__404">
          <div className="title__404">
              
          
            <button
              className="btn__404"
              onClick={() => navigate("/")}
            >
               
            </button>
          </div>
          </div>
          }
            ></Route>
        
        </Routes>
        </main>
      {/* <Particles  options={options} init={particlesInit} /> */}
      <Footer showModal={showModal} setShowModal={setShowModal}/>
    </>
  );
}

export default App;
