import React from "react";
import './footer.scss'
import { useLocation } from "react-router-dom";
import { Modal } from "../Modal/Modal";

export const Footer = ({setShowModal, showModal}) => {

  const { pathname } = useLocation();
  if (pathname === "/admin" && '*') {
    return <></>; }

  return (
    <div className='footer'> 
    <span> Dandelion</span>
    <span> - Nebelschwimmer ( ) </span>
    <span onClick={()=>{setShowModal(true)}} className="footer_admin_link"> </span>
        {showModal &&
                    <Modal setShowModal={setShowModal} showModal={showModal}>
                      <div className='modal_top'> </div>
                      <input className="sectionForm__form__input">
                      </input >
                      <div className='modal_btns_wrapper'>
                        <button className='modal_btn_warn' onClick={()=>{}}></button>
                        <button className='modal_btn' onClick={()=>{setShowModal(false)}}></button>
                      </div>
                    </Modal> 
                    }
    
    </div>
  )
}